import * as THREE from 'three'
//
import { 
  DeviceOrientationControls,
  OrbitControls
} from '../controls/controls.js'
//FBXLoader
import { FBXLoader } from '../loaders/loaders.js'
//scene
import { scene } from '../scene/scene.js'
//
import {
  pointLight,
  ambientLight
} from '../lights/lights.js'
//camera
import { camera } from '../camera/camera.js'
//renderer
import { renderer } from '../renderer/renderer.js'

import {
  isMobile
} from '../utils/utils.js'

//FBX
const FBXpath1 = process.env.NODE_ENV === 'development' ? './src/mod/ar.fbx' : 'mod/ar.fbx'
const FBXpath2 = process.env.NODE_ENV === 'development' ? './src/mod/icon0623.fbx' : 'mod/icon0623.fbx'

//
export function initThree(){
  //
  // scene.add(new THREE.AxesHelper(100))

  let mixer1 = null
  let mixer2 = null
  let controls = null
  const clock = new THREE.Clock()
  //FBX
  const loader = new FBXLoader()
  /**
   * 
   */
  loader.load(FBXpath1, function(obj) {
    scene.add(obj)
    // 
    obj.translateY(8)
    obj.scale.set(0.5, 0.5, 0.5)
    obj.rotateY(3)
    mixer1 = new THREE.AnimationMixer(obj)
    const AnimationAction = mixer1.clipAction(obj.animations[0])
    AnimationAction.play()
  })
  loader.load(FBXpath2, function(obj) {
    scene.add(obj)
    // 
    obj.translateY(-8)
    obj.rotateY(3)
    mixer2 = new THREE.AnimationMixer(obj)
    const AnimationAction = mixer2.clipAction(obj.animations[0])
    AnimationAction.play()
  })

  scene.add(pointLight)
  scene.add(ambientLight)
  //bodycanvas
  document.body.appendChild(renderer.domElement) 
  if(isMobile()){
    //
    controls = new DeviceOrientationControls(camera)
    render(true)
  }else{
    //
    controls = new OrbitControls(camera, renderer.domElement)
    render(false)
  }

  function render(isMobile = false) {
    //render
    requestAnimationFrame(render)
    if(isMobile){
      controls.update()
    }
    renderer.render(scene, camera) //
    if (mixer1 !== null && mixer2 !== null) {
      const delta = clock.getDelta()
      mixer1.update(delta)
      mixer2.update(delta)
    }
  }
}

//
export function initVideo(){
  if(navigator.mediaDevices){
    navigator.mediaDevices.getUserMedia({
      video: {
        width: window.innerWidth,
        height: window.innerHeight,
        facingMode: 'environment',
      }
    })
    .then(function(stream) {
      let video = document.querySelector('video')
      video.srcObject = stream
      video.onloadedmetadata = function (e) {
        video.play()
      }
    })
    .catch(function(err) {
      alert('')
    })
  }else{
    navigator.getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia
    if (navigator.getUserMedia) {
      navigator.getUserMedia({
          video: {
            width: window.innerWidth,
            height: window.innerHeight,
            facingMode: 'environment',
          }
        },
        function (stream) {
          let video = document.querySelector('video')
          video.srcObject = stream
          video.onloadedmetadata = function (e) {
            video.play()
          }
        },
        function (err) {
          alert('')
        }
      )
    } else {
      alert(',')
    }
  }
}

